import React, { lazy, Suspense } from "react";
import { Responsive } from "semantic-ui-react";
import { connect } from "react-redux";
import { T as FallBack } from "../../Fallback/T";
const ForComp = lazy(() => import("./forComp/forComp"));
const ForMob = lazy(() => import("./forMob/forMob"));
const ForTab = lazy(() => import("./forTablet/forTab"));

const MyProblems = (props) => {
  return (
    <div>
      <Responsive {...Responsive.onlyComputer}>
        <Suspense fallback={FallBack}>
          <ForComp deviceType="comp" {...props}></ForComp>
        </Suspense>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <Suspense fallback={FallBack}>
          <ForTab deviceType="tab" {...props}></ForTab>
        </Suspense>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <Suspense fallback={FallBack}>
          <ForMob deviceType="mob" {...props}></ForMob>
        </Suspense>
      </Responsive>
    </div>
  );
};

export default connect()(MyProblems);
